import axios from "axios";
import {useQuery} from "react-query";
const baseUrl=process.env.REACT_APP_BASE_URL

export const useSmartMenu = (username) => {
    const query = useQuery('keySmartMenu', () => axios.get(`${baseUrl}menu?username=${username}`).then(res => {
        return res.data;
    }, { refetchInterval: 1000 }))
    return {
        ...query,
        data: query.data
    }
}

export const useGetUsername =  (dataCookie) => {
    const query = useQuery('keyName', () => axios.get(`${baseUrl}?username=${dataCookie}`).then(res => {
        return res.data[0]
    },{ refetchInterval:1000}))
    return {
        ...query,
        data: query.data
    }
}